<template>
  <div class="smile__body">
    <div  v-for="(el, index) in checkedarr" :key="index" class="smile__item"  :style="{zIndex:10 + index, top: `${koef*el.top}px`, left: `${koef*el.left}px`}" :data-id="el.id">
<v-app> 
    <div class="d-flex flex-column justify-space-between align-center" :style="{ transform: 'rotate('+el.deg+'deg)'}">

    <div v-if="koef === 1" class="smile__nav-container" :class="dragg === true ? 'smile__nav-container--dragging':null"
    >
      
    </div>  
    <v-img
      :aspect-ratio="9/9"
      :width="koef*el.width"
      :contain="true"
      :src=el.src
      :lazy-src=el.src
      @mousedown.prevent="mouseDownSendData($event, el)"
      @touchstart="mouseDownSendDataMobile($event, el.id)"

    ></v-img>    
    </div></v-app>
  </div>
  </div>
</template>

<script>
export default {
  props:{
    checkedarr:{
      type: Array,
      required: true,
    },
    koef:{
      type: Number,
      required: true,
    },
    dragg:{
      type: Boolean,
      required: true,
    }
  },
  data: function(){
    return {
      tempElement: null,
    }
  },
  methods:{
    mouseDownSendData(event, el){
      let data = [event, el];
      this.$emit('receiveddataformousedown', data);
    },
    mouseDownSendDataMobile(event, elId){
      let checked = this.checkedarr.find(element=> element.id == elId);
      let data = [event, checked];
      this.$emit('receiveddataformousedownmobile', data);
   
    },
  },
  /*beforeUpdate(){
    let smiles = [...document.querySelectorAll('.smile__item')];
    smiles.forEach(el=>{
      let tempEL = el.dataset.id; 
      el.addEventListener("touchstart", this.updateElement(tempEL));
      el.addEventListener("touchstart", this.mouseDownSendDataMobile);
    })
  }*/
}
</script>

<style lang="scss">
.smile__item{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: absolute;
  //margin: 10px;
  cursor: pointer;
  //background: white;
}

.smile__body{
  position: absolute;
  width: 100%;
  height: 100%;
}

.smile__delete{
  padding: 7px 20px;
  background-color: salmon;
  color: white;
  border-radius: 5px;
  font-size: 16px;
  font-weight: 700;
  outline: none;
  margin-bottom: 10px;
}


.smile__nav-container{
  position: absolute;
  left: 0;
  top: -100px;
  z-index: 100;
}

.smile__nav-container--dragging{
 visibility: hidden;
 pointer-events: none;
}

</style>