<template>
  <div class="upload__container" @mouseup.prevent="mouseUp"  @touchend.passive="mouseUp">
    <div class="generator__button-container">
      <v-btn class="download__img generator__button--main"  @click="cropPopUp()">
        Загрузить картинку
      </v-btn>

      <v-btn class="download__img download__img--secondary generator__button--main"  @click="changeBGblur()">
        Размыть фон
      </v-btn>

      <v-btn class="download__img download__img--secondary generator__button--main"  @click="changeOneColor()">
        Залить цветом
      </v-btn>
        <div class="change__color" v-if="oneColor"> 
       <color-picker
            theme="light"
            :color="colour"
            :sucker-hide="false"
            @changeColor="changeColor"
        />
    </div>
      <v-btn class="download__img download__img--save generator__button--main"  @click="download()">
        Скачать
      </v-btn>    
    </div>
    <smile-input @receivedsmile="addToArrCheckedSmile"></smile-input>
  
    <div v-if="croppingStart" class="upload__crop-container">
      <div class="upload__crop-body">
        <cropping-cropa @receiveimg="updateRenderedImg" @closepopup="croppingStart=false"></cropping-cropa>
      </div>
    </div>
    
    <div class="img__mini-container">
    <div class="img__mini">
      <div class="img__container--border" v-if="img !== 0">
          <div class="img__main-container--mini"
            :style="color"
            @mousemove.prevent="mouseMove($event)"
            @touchmove.passive="mouseMoveMobile"
            >
              <checked-smiles :dragg="dragging" :koef="1" :checkedarr="checkedSmile" @receiveddataformousedown="saveDataForMouseDown"
              @receiveddataformousedownmobile="saveDataForMouseDownMobile"
              ></checked-smiles>
              <img :src="img" @onload="render()" class="img__main-render"/>
              <img :src="bgformini!==0 ? bgformini : null" alt="blur" class="img__mini--bg">
          </div>
      </div> 
    </div>
    <div class="smile__item-corection--container"  v-if="smileForManipulation !== 0">
      <v-app>
        <div class="smile__item-corection-title">Размер</div>
        <v-slider
        v-model="checkedSmile[findElfromSmileChecked()].width"
        class="align-self-stretch"
        min="60"
        max="300"
        step="1"
      ></v-slider>  
      <div class="smile__item-corection-title">Поворот</div>
      <v-slider 
        v-model="checkedSmile[findElfromSmileChecked()].deg"
        class="align-self-stretch"
        min="0"
        max="360"
        step="1"
      ></v-slider> 
      <button @click="deleteSmile(findElfromSmileChecked())" class="smile__delete">Удалить</button>   
      </v-app>
      </div>
</div>

    <div id="render__hide" v-if="img !== 0">
      <div class="img__main-container"
        :style="color" id="render"
        @mousemove.prevent="mouseMove($event)"
        >
          <checked-smiles :koef="3" :checkedarr="checkedSmile" :dragg="dragging"  @receiveddataformousedown="saveDataForMouseDown"></checked-smiles>
          <img :src="img" @onload="render()" class="img__main-render"/>
      </div>
    </div>  

  </div>
</template>

<script>
import croppingCropa from '@/components/croppingCropa';
import smileInput from '@/components/smileInput';
import checkedSmilesComp from '@/components/checkedSmilesComp';
import colorPicker from '@caohenghu/vue-colorpicker';
import { gradientPicker } from 'vue-color-gradient-picker';

import html2canvas from 'html2canvas';
import { saveAs } from 'file-saver';

import screenshot from 'image-screenshot' //for saving blur effect on the background

 
export default {
  components:{
    'cropping-cropa': croppingCropa,
    'smile-input': smileInput,
    'checked-smiles': checkedSmilesComp,
    'gradient-picker': gradientPicker,
     colorPicker,
  },
  data: function (){
    return{
      myCroppa: {}, 
      img: 0,
      background: 0,
      croppingStart: false,
      oneColor: false,
      colour: '#000',
      checkedSmile:[],
      bgformini: 0,

      //smiles
      tempX:0,
      tempY:0,
      tempel:0,
      dragging: false,
      startX: 0,
      startY: 0,
      smileForManipulation: 0,

      //gradient
      gradient: {
        degree: 0,
        points: [
          {
            left: 0,
            red: 0,
            green: 0,
            blue: 0,
            alpha: 1
          },
          {
            left: 100,
            red: 255,
            green: 0,
            blue: 0,
            alpha: 1
          }
        ]      
      },
     }
  },
  methods: {
     render(){ctx.drawImage(this.img, 0, 0)},
     cropPopUp(){
       this.croppingStart = true;
       this.oneColor = false;
     },
     updateRenderedImg(e) {
       this.img = e[0];
       this.background = e[1];
       this.croppingStart = false;
     },
     changeBGblur(){
       this.oneColor = false; 

       if(this.background !== 0){
        this.colour = 'rgba(0, 0, 0, 0)';
        
        if(document.querySelector('#forsavingasimg') === null){
          const tempBg = document.createElement("img");
          tempBg.setAttribute("id", "forsavingasimg");
          tempBg.src = this.background;
          tempBg.classList.add('img__background');
      
          const parent = document.querySelector(".img__main-container");
          parent.appendChild(tempBg);
          
          screenshot(tempBg); //fix problem with first rendering (now idea how it works, dont touch)
          setTimeout(_=>{
            screenshot(tempBg).then(url=>{
              tempBg.src = url;
              this.bgformini = url;});
              document.querySelector("#forsavingasimg").classList.remove("img__background");
              document.querySelector("#forsavingasimg").classList.add("img__background--final");
          }, 100)       
        }else{
          document.querySelector('#forsavingasimg').src = this.background;
          document.querySelector("#forsavingasimg").classList.add("img__background");
          setTimeout(_=>{
            const imgforRender = document.querySelector('#forsavingasimg');
        
            if(imgforRender !==null){
              screenshot(imgforRender).then(url=>{
                imgforRender.src = url;
                document.querySelector("#forsavingasimg").classList.remove("img__background");
                document.querySelector("#forsavingasimg").classList.add("img__background--final");
                this.bgformini = url;
              });
            }
          }, 100)
        }
      }  
     },
     changeOneColor(){
      this.colour = 'rgba(0, 0, 0, 1)';
      const elem = document.querySelector('#forsavingasimg');
      elem.parentNode.removeChild(elem);
      this.oneColor = true;
     },
     changeColor(color) {
      const { r, g, b, a } = color.rgba
      this.colour = `rgba(${r}, ${g}, ${b}, ${a})`
     },
  
     download(){
      this.oneColor = false; 
      const checked = document.getElementById('render');
      if(checked !== null){
        html2canvas(
          checked,
          {
            width: 1080,
            height: 1080,
            onclone: function (clonedDoc) {
              clonedDoc.getElementById('render').style.visibility = 'visible';
            } 
          })
              .then(canvas => {
                canvas.toBlob(function(blob) {
                const Hourse = new Date().getHours();
                let Minutes = new Date().getMinutes();
                let Month = new Date().getMonth() +1;
                let Day = new Date().getDate();

                Month < 10 ? Month = '0'+Month:null;
                Minutes  < 10 ? Minutes = '0'+Minutes:null;
                Day < 10 ? Day = '0'+Day:null;
                saveAs(blob, `${'insta_' + Day + '-' + Month + '_'+ Hourse + '-' + Minutes}.png`);
                });
              })
      }
    
     },
     addToArrCheckedSmile(e){
      this.oneColor = false;
      this.checkedSmile.push({id: this.checkedSmile.length, name: e.name, src: e.src, top:0, left:0, width: 60, deg: 0});
      this.smileForManipulation = 0;
     },
     saveDataForMouseDown(data){

       this.smileForManipulation = data[1];

       this.tempX = data[0].clientX;
       this.tempY = data[0].clientY;
       //console.log(data);
       this.startX = data[1].left;
       this.startY = data[1].top;
       this.tempel = data[1];
       this.dragging = true;
     },
     saveDataForMouseDownMobile(data){
     
       this.smileForManipulation = data[1];

       this.tempX = data[0].touches[0].clientX;
       this.tempY = data[0].touches[0].clientY;
       //console.log(data);
       this.startX = data[1].left;
       this.startY = data[1].top;
       this.tempel = data[1];
       this.dragging = true;
     },
     mouseMove(event){
       if(this.dragging){
        let el = this.checkedSmile.find(el=>el == this.tempel);
        
        el.left = this.startX + (event.clientX - this.tempX);
        el.top =  this.startY + (event.clientY - this.tempY);
       }
     },
      mouseMoveMobile(event){
       if(this.dragging){
        let el = this.checkedSmile.find(el=>el == this.tempel);
        
        el.left = this.startX + (event.touches[0].clientX - this.tempX);
        el.top =  this.startY + (event.touches[0].clientY - this.tempY);
       }
     },
     mouseUp(event){ this.dragging = false;},
     findElfromSmileChecked(){
       let checked = this.checkedSmile.find(el=> el.id == this.smileForManipulation.id);
       if(checked !== undefined){
         return this.checkedSmile.indexOf(checked);
       }
     },
      deleteSmile(index){
      this.checkedSmile.splice(index, 1);
      this.smileForManipulation = 0;
    },
   },
   computed:{
     color(){  //for rendering the color on the bg
        let bgColor;
        return bgColor = {backgroundColor: this.colour};
     }
   }
}
</script>

<style lang="scss">

.img__container--border{
  outline: 2px solid salmon;
  max-width: max-content;
}

.upload__container{
  margin-top: 50px;
  padding: 20px;
  position: relative;
}

.download__img{
  padding: 20px !important;
  margin: 10px;
  font-weight: 700 !important;
  color: white !important;
  background-color: blueviolet !important
  
}

.download__img--secondary{
  color: white !important;
  background-color: rgb(185, 135, 231) !important
}

.download__img--save{
  background-color: green !important;
}
.img__main-container{
  max-width: max-content;
  position: relative;
  overflow: hidden;
  max-height: 1080px;
}

.img__main-container--mini{
  max-width: max-content;
  position: relative;
  overflow: hidden;
  max-height: 360px;
}

.smile__item-corection--container{
  width: 360px;
  padding: 20px;
}



.smile__container{
  border: 2px solid salmon;
  max-width: max-content;
  padding: 10px;

  display: flex;
  flex-wrap: wrap;
  margin: 10px;
}

.smile__weapper{
  margin: 10px;
  width: 70px;
  height: 70px;
  background: sandybrown;
  cursor: pointer;
}

.img__main-render{
  width: 100%;
  pointer-events: none;
}

.change__color{
  display: flex;
  justify-content: center;
  margin-bottom: 30px;
}

.sucker{
  display: none;
}

.colors .item:nth-child(8n+1),
.colors .item{
  margin: 10px !important;
}

.hu-color-picker.light{
  width: 300px !important;
}

.img__background{
  background-size: 100% 100%;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: -1;
  background-repeat: no-repeat;

  backdrop-filter: blur(10px);
  -webkit-backdrop-filter:blur(10px);
  transform: scale(1.1);
}

.img__background--final{
  background-size: 100% 100%;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: -1;
  background-repeat: no-repeat;
  transform: scale(1.1);
}

#render{
  visibility: hidden;
  pointer-events: none;
}

.img__mini--bg{
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  z-index: -1;
  background-size: 100% 100%;
  height: 360px;
  transform: scale(1.1);
  background-position: center;
  background-repeat: no-repeat;
}

.change__color{
  position: absolute;
  z-index: 9999;
  top: 0;
  right: 20px;
}

.img__mini{
  height: 360px;
  width: 360px;
}

.img__mini-container{
  display: flex;
  flex-wrap: wrap;
  padding: 10px;
  max-width: fit-content;
}

@media (max-width: 1024px){
  .generator__button--main{
    width: calc(100% - 20px)
  }
  .smile__container{
    max-width: calc(100% - 20px);
    width: calc(100% - 20px);
  }
  .change__color{
    position: relative;
    top: 20px;
    left: 0;
    width: 100%;
  }
  
  .hu-color-picker.light{
    width:  calc(100% - 20px) !important;
  }
}

</style>