<template>
  <div class="popup">
    <div class="popup__weapper">
      <div class="popup__main-content">
        <croppa
        v-model="myCroppa"
        :width="1080"
        :height="1080"
        :quality="1"
        initial-size="contain"
        :accept="'image/*'" 
        />
        <button class="popup__btn-save" @click="getImg()">Сохранить</button>
        <button class="popup__btn-save" @click="closePopUp()">Закрыть</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data: function (){
    return{
      myCroppa: {},
      savedImg: 0,
    }
  },
  methods: {
    uploadCroppedImage() {
      this.myCroppa.generateBlob(blob => {}, 'image/jpeg', 1) 
    },
    getImg(el){
      const tempImg = this.myCroppa.getCanvas().toDataURL();

      const tempPhoto = this.myCroppa.getChosenFile();
      if(tempPhoto !== undefined){
        this.savedImg = URL.createObjectURL(tempPhoto);
        let photoes = [tempImg, this.savedImg]
        this.$emit('receiveimg',photoes);
      } 
     },
     closePopUp(){
      this.$emit('closepopup');
     },
   }
}
</script>

<style lang="scss">
.popup{
  display: flex;
  justify-content: center;
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  z-index: 100;
  
  min-height:100%;
  background-color:rgba($color: #000000, $alpha: 0.8);
}

.popup__weapper{
  transform: translate(0, -35%) scale(0.3);
}

.popup__main-content{
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 72px;
}

.popup__main-content{
  padding-top: 100px;
}

.popup__btn-save{
  margin-top: 50px;
  background-color: white;
  padding: 60px;
  border-radius: 30px;
}
</style>