<template>
    <div class="smile__container">
      <div class="smile__weapper" v-for="(el, index) in smile" :key="index" @click="chekedSmile(el)">
        <img :src="el.src" :alt="el.name" class="smile__image">
      </div>
    </div>
</template>

<script>
export default {
  data: function(){
    return {
      smile:[
        {id: 0,
         src: require('@/assets/smile.png'),
         name: '1',
        },
        {id: 1,
         src: require('@/assets/smile.png'),
         name: '2',
        },
        {id: 3,
         src: require('@/assets/Smiley.png'),
         name: '3',
        },
      ],
    }
  },
  methods:{
    chekedSmile(el){
       this.$emit('receivedsmile', el);
    }
  }
}
</script>

<style lang="scss">
  .smile__image{
    object-fit: contain;
    width: 100%;
  }
</style>